import styled from 'styled-components';

export const Page = styled.div `
height: 80%;

&:after{
        content: "";
        clear: both;
        display: table;
}
`;

export const ClearList = styled.div`
&:after{
        content: "";
        clear: both;
        display: table;
}
`;

export const ProfileBlock = styled.div `
float: left;
margin-left: 8%;
margin-right: 2%;
width: 60%;
height: 90%;
border-radius: 20px;

@media screen and (max-width: 960px) {
        margin-left: 2%;
        width: 96%;
}

`;


export const CoverPhoto = styled.div `
width: 100%;
height: 300px;
border-radius: 20px;
position:relative;
z-index: 10;
background: grey;
`;

export const ProfileImageTab = styled.div `
border-radius: 50%;
position: relative;
top: 225px;
background-color: #fff;
height: 150px;
width: 150px;
left: 2%;
z-index: 12;
`;


export const UserImageTab = styled.div ``;

export const ProfileName = styled.h1`
color: #fff;

`;
export const SideBlock = styled.div `
float: left;
width: 20%;
border-radius: 20px;
height: 50%;
margin-top: 30px;

@media screen and (max-width: 960px) {
        width: 96%;
        height: 60%;;
        margin-left: 2%;
        margin-right: 2%;
}

&:after{
        content: "";
        clear: both;
}
`;

export const InsideBlock = styled.div `
float: left;
width: 100%;
border-radius: 20px;
background-color: #25323d;
height: 100%;
margin-bottom: 8px;

@media screen and (max-width: 960px) {
        height: 60%;
        width: 98%;
        margin-left: 1%;
}
`;

export const ProjectInformation =styled.div`

`;

export const ProjectCost =styled.h2`
color: #44c0c4;
margin-top: 20px;
`;


export const ProfileParagraph = styled.p`
color: grey;
font-size: 18px;
`;

export const ProjectList = styled.div`

&:after{
        content: "";
        clear: both;
}
`;

export const ProjectReview = styled.div``;

