import React from "react";
import { InfoSec, ProjectHeading } from "./InfoSectionElements";
import RFQCreateForm from "../Forms/RFQCreateForm";
import { BackPageButton } from "../Modals/ModalElements";
import { Link } from "react-router-dom";

const RFQCreateInfoSection = ({ lightBg }) => {
  return (
    <>
      <InfoSec
        lightBg={lightBg}
        style={{ marginBottom: "0", paddingBottom: "0", paddingTop: "0" }}
      >
        <Link to="/RFQ">
          <BackPageButton style={{ marginLeft: "30%", marginBottom: "50px" }} />{" "}
        </Link>

        <ProjectHeading
          style={{
            // align: "center",
            marginLeft: "0",
            fontSize: "30px",
            marginBottom: "0",
            textAlign: "center",
          }}
        >
          Create a New Project
        </ProjectHeading>
        <p
          style={{
            marginLeft: "30%",
            width: "40%",
            textAlign: "center",
            color: "#9eacb7",
          }}
        >
          Create a Project Request for Quote(RFQ) for users to submit their
          quotes. Relevant users will be notified of your request.
        </p>
      </InfoSec>
      <InfoSec lightBg={lightBg} style={{ marginTop: "0", paddingTop: "20px" }}>
        <RFQCreateForm />
      </InfoSec>
    </>
  );
};

export default RFQCreateInfoSection;
