import React from "react";
import { FaSearch } from "react-icons/fa";
import {
  SearchBox,
  SearchInput,
  SearchIconBtn,
  SearchInputBox,
  SearchIconBtnWrap,
} from "./SearchbarElements";

const Searchbar = () => {
  return (
    <>
      <SearchBox>
        <SearchInputBox>
          <SearchInput
            type="text"
            name=" "
            placeholder="What are you looking for"
          />
          <SearchIconBtnWrap>
            <SearchIconBtn>
              <FaSearch aria-hidden="true" />{" "}
            </SearchIconBtn>
          </SearchIconBtnWrap>
        </SearchInputBox>
      </SearchBox>
    </>
  );
};

export default Searchbar;
