import React, { useState, useEffect } from "react";
import { useFilePicker } from "use-file-picker";
import { animateScroll as scroll } from "react-scroll";
import GlobalStyle, { Button } from "../../globalStyles";
import FormModal from "../Modals/FormModal";
import { Link } from "react-router-dom";
import { ProjectHeading } from "../InfoSection/InfoSectionElements";
import {
  Select,
  RFQDesc,
  RFQMiniStatement,
  RFQSearchbar,
  SearchInputBox,
  SearchInput,
  RFQInput,
  RFQTextArea,
} from "./RFQFormElements";
import { FaPlus, FaSearch } from "react-icons/fa";
import { MdClose } from "react-icons/md";

const RFQCreateForm = () => {
  const toggleTop = () => {
    scroll.scrollToTop();
  };
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener("resize", showButton);

  const [showModal, setShowModal] = useState(false);
  const openModal = () => {
    setShowModal((prev) => !prev);
  };

  const [
    openFileSelector,
    { filesContent, loading, errors, plainFiles, clear },
  ] = useFilePicker({
    multiple: true,
    readAs: "DataURL", // availible formats: "Text" | "BinaryString" | "ArrayBuffer" | "DataURL"
    // accept: '.ics,.pdf',
    accept: [".json", ".pdf", "image/*"],
    limitFilesConfig: { min: 1, max: 3 },
    // minFileSize: 1, // in megabytes
    // maxFileSize: 1,
    // readFilesContent: false, // ignores file content
  });

  if (errors.length) {
    return (
      <div>
        <button onClick={() => openFileSelector()}>
          Something went wrong, retry!{" "}
        </button>
        {errors[0].fileSizeTooSmall && "File size is too small!"}
        {errors[0].fileSizeToolarge && "File size is too large!"}
        {errors[0].readerError && "Problem occured while reading file!"}
        {errors[0].maxLimitExceeded && "Too many files"}
        {errors[0].minLimitNotReached && "Not enought files"}
      </div>
    );
  }

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <>
      <form>
        <RFQInput type="text" placeholder="Project Name" required />
        <RFQInput type="text" placeholder="Client (Optional)" />
        <RFQInput type="text" placeholder="Tender Number (Optional)" />
        <Select style={{ marginBottom: "20px" }}>
          <option>Value</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <Select style={{ marginBottom: "20px" }}>
          <option>Location</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <Select style={{ marginBottom: "20px" }}>
          <option>Sector</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <Select style={{ marginBottom: "20px" }}>
          <option>Contract Type</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <Select style={{ marginBottom: "20px" }}>
          <option>Issue Date</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <Select style={{ marginBottom: "20px" }}>
          <option>Expiry Date</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <Select style={{ marginBottom: "20px" }}>
          <option>Site Meeting</option>
          <option>Supplier</option>
          <option>Contractor</option>
        </Select>

        <RFQTextArea placeholder="Project Overview Description" required />

        <hr
          style={{
            border: "1px solid #25323d",
            marginBottom: "20px",
            width: "40%",
            marginLeft: "30%",
          }}
        />

        <ProjectHeading
          style={{
            // align: "center",
            marginLeft: "30%",
            fontSize: "22px",
            marginBottom: "20px",
            textAlign: "left",
          }}
        >
          Item Quote Requirements
        </ProjectHeading>
        <p
          style={{
            marginLeft: "30%",
            marginBottom: "20px",
            width: "40%",
            textAlign: "left",
            fontSize: "12px",
            color: "#9eacb7",
          }}
        >
          Items that require quotes for this project can be added here. Each
          Item requires its own description and details.
        </p>

        <div style={{ color: "#fff" }}>
          <br />
          {/* If readAs is set to DataURL, You can display an image */}
          {!!filesContent.length && (
            <img
              style={{ height: "50px", width: "40%", marginLeft: "30%" }}
              src={filesContent[0].content}
            />
          )}
          <br />
          {plainFiles.map((file) => (
            <div style={{ width: "40%", marginLeft: "30%" }} key={file.name}>
              {file.name}
            </div>
          ))}
          <Button
            onClick={() => openFileSelector()}
            style={{
              fontSize: "14px",
              backgroundColor: "#25323d",
              border: "1px solid #25323d",
              width: "40%",
              height: "50px",
              color: "#9eacb7",
              marginLeft: "30%",
              marginRight: "1%",
              borderRadius: "5px",
              marginBottom: "20px",
              paddingLeft: "10px",
            }}
          >
            <FaPlus /> Add Another Item
          </Button>
          <Button
            style={{
              fontSize: "18px",
              backgroundColor: "#101522",
              border: "none",
              marginLeft: "30%",
              marginRight: "1%",
              width: "10%",
              height: "20px",
              color: "#9eacb7",
              //   borderRadius: "5px",
              marginBottom: "20px",
              //   paddingLeft: "10px",
            }}
            onClick={() => clear()}
          >
            Clear Selection
          </Button>
          <br />
        </div>

        <hr
          style={{
            border: "1px solid #25323d",
            marginBottom: "20px",
            width: "40%",
            marginLeft: "30%",
          }}
        />

        <ProjectHeading
          style={{
            // align: "center",
            marginLeft: "30%",
            fontSize: "22px",
            marginBottom: "20px",
            textAlign: "left",
          }}
        >
          Project Media
        </ProjectHeading>
        <p
          style={{
            marginLeft: "30%",
            marginBottom: "20px",
            width: "40%",
            textAlign: "left",
            fontSize: "12px",
            color: "#9eacb7",
          }}
        >
          Add project media here, JPG, PNG, Zipped folder formats are
          acceptable.
        </p>

        <div style={{ color: "#fff" }}>
          <br />
          {/* If readAs is set to DataURL, You can display an image */}
          {!!filesContent.length && (
            <img
              style={{ height: "50px", width: "40%", marginLeft: "30%" }}
              src={filesContent[0].content}
            />
          )}
          <br />
          {plainFiles.map((file) => (
            <div style={{ width: "40%", marginLeft: "30%" }} key={file.name}>
              {file.name}
            </div>
          ))}
          <Button
            onClick={() => openFileSelector()}
            style={{
              fontSize: "14px",
              backgroundColor: "#25323d",
              border: "1px solid #25323d",
              width: "40%",
              height: "50px",
              color: "#9eacb7",
              marginLeft: "30%",
              marginRight: "1%",
              borderRadius: "5px",
              marginBottom: "20px",
              paddingLeft: "10px",
            }}
          >
            {" "}
            <FaPlus /> Add Another Item
          </Button>
          <br />
          <Button
            style={{
              fontSize: "18px",
              backgroundColor: "#101522",
              border: "none",
              marginLeft: "30%",
              marginRight: "1%",
              width: "10%",
              height: "20px",
              color: "#9eacb7",
              //   borderRadius: "5px",
              marginBottom: "20px",
              //   paddingLeft: "10px",
            }}
            onClick={() => clear()}
          >
            Clear selection
          </Button>
          <br />
        </div>

        <hr
          style={{
            border: "1px solid #25323d",
            marginBottom: "20px",
            width: "40%",
            marginLeft: "30%",
          }}
        />

        <Link
          onClick={toggleTop}
          style={{ textDecoration: "none", color: "#fff" }}
          to="/RFQSpec"
        >
          <Button
            style={{
              marginBottom: "20px",
              marginLeft: "30%",
              width: "40%",
              height: "50px",
            }}
          >
            Publish Project
          </Button>
        </Link>
        <RFQMiniStatement style={{ color: "#9eacb7" }}>
          Save as Draft
        </RFQMiniStatement>
      </form>
    </>
  );
};

export default RFQCreateForm;
