import React, { useState, useRef, useEffect, useCallback } from "react";
import { useSpring, animated } from "react-spring";
import { Button } from "../../globalStyles";
import { Link } from "react-router-dom";
import {
  Background,
  ModalContent,
  ModalWrapper,
  CloseModalButton,
  FormWrap,
  FormContent,
  Form,
  FormH1,
  FormInput,
  FormButton,
  Text,
  NextPageButton,
  PrevPageButton,
} from "./ModalElements";

const LoginModal = ({ showModal, setShowModal }) => {
  const modalRef = useRef();

  const animation = useSpring({
    config: {
      duration: 250,
    },
    opacity: showModal ? 1 : 0,
    transform: showModal ? `translateY(0%)` : `translateY(-100%)`,
  });

  const closeModal = (e) => {
    if (modalRef.current === e.target) {
      setShowModal(false);
    }
  };

  const keyPress = useCallback(
    (e) => {
      if (e.key === "Escape" && showModal) {
        setShowModal(false);
      }
    },
    [setShowModal, showModal]
  );

  useEffect(() => {
    document.addEventListener("keydown", keyPress);
    return () => document.removeEventListener("keydown", keyPress);
  }, [keyPress]);

  const [page, setPage] = useState(1);
  const [data, setData] = useState({
    user: {},
    profile: {},
    settings: {},
  });

  function goNextPage() {
    if (page === 4) return;
    setPage((page) => page + 1);
  }
  function goPrevPage() {
    setPage((page) => page - 1);
  }

  function updateData(type, newData) {
    setData((data) => {
      return { ...data, [type]: newData };
    });
  }

  function submit() {
    fetch("/api/form", { method: "POST", body: JSON.stringify(data) });
  }

  return (
    <>
      {showModal ? (
        <Background
          ref={modalRef}
          onClick={closeModal}
          style={{
            marginTop: "-66%",
            marginLeft: "-31.5%",
          }}
        >
          <animated.div style={animation}>
            <ModalWrapper
              showModal={showModal}
              style={{ height: "500px", width: "480px" }}
            >
              <ModalContent style={{ borderRadius: "10px" }}>
                {page === 1 && <></>}
                {page === 2 && (
                  <FormWrap>
                    <FormContent>
                      <FormH1
                        style={{
                          marginTop: "50px",
                          marginLeft: "20px",
                          marginBottom: "0",
                        }}
                      >
                        Hi, <user />, Welcome to Afriserv
                      </FormH1>
                      <Text style={{ fontSize: "12px" }}>
                        Enhance your presence by joining your business page, or
                        creating a new page for your business
                      </Text>
                      <Form action="#">
                        <FormInput
                          type="text"
                          placeholder="Search for my Company"
                          required
                          style={{ marginTop: "20px", textAlign: "center" }}
                        />

                        <Text
                          style={{
                            fontSize: "14px",
                            color: "#000",
                            marginTop: "20px",
                            textAlign: "center",
                            marginBottom: "20px",
                          }}
                        >
                          Or
                        </Text>

                        <FormButton type="submit">Add a New Company</FormButton>
                        <p
                          className="text-muted text-center mt-3 mb-0"
                          style={{ fontSize: "12px", marginTop: "20px" }}
                        >
                          <Link
                            to=""
                            className="text-primary ml-1"
                            style={{
                              fontSize: "12px",
                              color: "grey",
                              marginTop: "20px",
                              textAlign: "center",
                            }}
                          >
                            Skip for now
                          </Link>
                        </p>
                      </Form>
                    </FormContent>
                  </FormWrap>
                )}
                {page === 3 && (
                  <FormWrap>
                    <FormContent>
                      <FormH1>Log In</FormH1>
                      <Form action="POST">
                        <FormInput
                          type="email"
                          placeholder="   Email"
                          required
                        />
                        <FormInput
                          type="password"
                          placeholder="   Password"
                          required
                        />
                        <FormButton type="submit">Log in</FormButton>
                        <Text>Forgot password</Text>
                        <p className="text-muted text-center mt-3 mb-0">
                          Don't have an account?
                          <Link to="" className="text-primary ml-1">
                            Sign Up
                          </Link>
                        </p>
                      </Form>
                    </FormContent>
                  </FormWrap>
                )}
                {page === 4 && <OnboardingFour />}

                {page !== 1 && (
                  <PrevPageButton
                    aria-label="Previous Page modal"
                    onClick={goPrevPage}
                    style={{
                      color: "#101522",
                    }}
                  />
                )}

                {page !== 4 && (
                  <NextPageButton
                    aria-label="Next Page modal"
                    onClick={goNextPage}
                    style={{
                      color: "#101522",
                    }}
                  />
                )}
                {page === 4 && (
                  <button type="submit" onClick={submit}>
                    Submit
                  </button>
                )}
                <CloseModalButton
                  aria-label="Close modal"
                  onClick={() => setShowModal((prev) => !prev)}
                  style={{
                    color: "#101522",
                  }}
                />
              </ModalContent>
            </ModalWrapper>
          </animated.div>
        </Background>
      ) : null}
    </>
  );

  function SignupOnboarding({ data, update }) {
    const newData = {};

    return (
      <>
        <FormWrap>
          <FormContent>
            <FormH1 style={{ marginTop: "50px", marginLeft: "20px" }}>
              Sign Up
            </FormH1>
            <Form action="#">
              <FormInput type="text" placeholder="   First Name" required />
              <FormInput type="password" placeholder="   Last Name" required />

              <FormInput
                style={{ marginTop: "16px" }}
                type="text"
                placeholder="   SA ID/ Passport Number"
                required
              />
              <Text style={{ fontSize: "12px" }}>
                We'll email you notifications about bids, confirmations,
                verifications and receipts
              </Text>

              <FormInput
                style={{ marginTop: "16px" }}
                type="email"
                placeholder="   Email"
                required
              />

              <FormInput
                style={{ marginTop: "16px" }}
                type="password"
                placeholder="   Password"
                required
              />
              <Text style={{ fontSize: "12px" }}>
                By selecting Agree and continue below, I agree to{" "}
                <Link style={{ fontSize: "12px", color: "grey" }}>
                  Afriserv's Terms of Service, Payments Terms of Service,
                  Privacy Policy
                </Link>{" "}
                and{" "}
                <Link style={{ fontSize: "12px", color: "grey" }}>
                  Nondiscrimination Policy
                </Link>{" "}
              </Text>

              <FormButton type="submit">Agree and Continue</FormButton>
              <p
                className="text-muted text-center mt-3 mb-0"
                style={{ fontSize: "12px" }}
              >
                <Link
                  to=""
                  className="text-primary ml-1"
                  style={{ fontSize: "12px", color: "grey" }}
                >
                  Already have an account?
                </Link>
              </p>
            </Form>
          </FormContent>
        </FormWrap>
      </>
    );
  }

  function LoginOnboarding({ data, update }) {
    return (
      <FormWrap>
        <FormContent>
          <FormH1>Log In</FormH1>
          <Form action="POST">
            <FormInput type="email" placeholder="   Email" required />
            <FormInput type="password" placeholder="   Password" required />
            <FormButton type="submit">Log in</FormButton>
            <Text>Forgot password</Text>
            <p className="text-muted text-center mt-3 mb-0">
              Don't have an account?
              <Link to="" className="text-primary ml-1">
                Sign Up
              </Link>
            </p>
          </Form>
        </FormContent>
      </FormWrap>
    );
  }

  function OnboardingThree({ data, update }) {
    return <div>i am page three</div>;
  }

  function OnboardingFour({ data, update }) {
    return <div>i am page four</div>;
  }
};

export default LoginModal;
