import styled from "styled-components";
import { Container } from "../../globalStyles";
import { NavLink, Link } from "react-router-dom";
import Img from "../Navbar/logo.svg";


export const Dropbtn = styled.button` 
  background-color: #04AA6D;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;

  &:hover {
    background-color: #3e8e41;
  }
`;

export const DropdownB  = styled.div`
  position: relative;
  display: inline-block;

  &:hover {
    background-color: #3e8e41;
    display: block;
  }
`;

export const lia = styled.ul`
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
`;

export const DropdownContent = styled.div `
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;

  &:hover {
    background-color: #3e8e41;
    display: block;
  }

`;



export const Nav = styled.nav`
  background: #101522;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  position: sticky;
  top: 0;
  z-index: 999;
`;

export const NavbarContainer = styled(Container)`
  display: flex;
  justify-content: space-between;
  height: 80px;

  ${Container}
`;

export const NavLogo = styled(Link)`
  color: white;
  justify-self: flex-start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
`;

export const LogoImg = styled.div`
  width: 140px;
  height: 270px;
  background: url(${Img}) 45% no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
`;

export const MobileIcon = styled.div`
  display: none;

  @media screen and (max-width: 960px) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
`;

export const NavMenu = styled.div`
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  margin-right: 10%;

  @media screen and (max-width: 960px) {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: ${({ click }) => (click ? 0 : "-100%")};
    opacity: 1;
    transition: all 0.5s ease;
    background: #101522;
  }
`;

export const NavItem = styled.div`
  height: 80px;
  border-bottom: 2px solid transparent;

  &:hover {
    border-bottom: 2px solid #44c0c4;
  }

  @media screen and (max-width: 960px) {
    width: 100%;

    &:hover {
      border: none;
    }
  }
`;

export const NavLinks = styled(NavLink)`
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;

  &.active {
    color: #44c0c4;
    border-bottom: 2px solid #44c0c4;
  }
  &:hover {
    color: #44c0c4;
  }

  @media screen and (max-width: 960px) {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;

    &:hover {
      color: #44c0c4;
      transition: all 0.3s ease;
    }

    &.active {
      color: #44c0c4;
      border: none;
    }
  }
`;

export const NavLinksLog = styled(NavLink)`
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;

  &:hover {
    color: #44c0c4;
  }

  @media screen and (max-width: 960px) {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;

    &:hover {
      color: #44c0c4;
      transition: all 0.3s ease;
    }
  }
`;

export const NavItemBtn = styled.li`
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;

  @media screen and (max-width: 960px) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
`;

export const NavBtnLink = styled(Link)`
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 8px 16px;
  height: 100%;
  width: 100%;
  border: none;
  outline: none;
`;

export const Select = styled.select`
  color: #fff; /*
  font-weight: 300;
  font-size: 18px;
  background-color: #101522;
  border: 0px;
  outline: 0px;*/
  width: 5px;

  &:hover {
  }

  option {
    color: #101522;
    width: 100px;
    background-color: #fff;
    padding: 5px;
  }
`;

export const HoverBtn = styled.div`

  &:hover {
    margin-top: 240px;
  }

`;

