import React from "react";
import GlobalStyle from "./globalStyles";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Navbar, InfoSection, Footer } from "./components";
import Home from "./pages/HomePage/Home";
import Projects from "./pages/Projects/Projects";
import Explore from "./pages/Explore/Explore";
import RFQ from "./pages/RFQ/RFQ";
import RFQCreate from "./pages/RFQ/RFQCreate";
import RFQSpec from "./pages/RFQ/RFQSpec";
import ProjectDetails from "./pages/ProjectDetails/ProjectDetails";
import Bidroom from "./pages/BidRoom/Bidroom";

function App() {
  return (
    <Router>
      <GlobalStyle />
      <Navbar />
      <Route path="/" exact component={Home} />
      <Switch>
        <Route path="/RFQ" exact component={RFQ} />
        <Route path="/projects" exact component={Projects} />
        <Route path="/Explore" exact component={Explore} />
        <Route path="/RFQCreate" exact component={RFQCreate} />
        <Route path="/RFQSpec" exact component={RFQSpec} />
        <Route path="/ProjectDetails" exact component={ProjectDetails} />
        <Route path="/Bidroom" exact component={Bidroom} />
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
