import React from "react";
import {
  InfoSec,
  InfoRow,
  InfoColumn,
  TextWrapper,
  TopLine,
  Heading,
  Subtitle,
  ImgWrapper,
  Img,
  ProjectHeading,
  Para
} from "./InfoSectionElements";
import { Container } from "../../globalStyles";
import Dropdown from "../Dropdown/Dropdown.js";
import projectsProp from "../Dropdown/Data/projectsProp.json";
import ProjectsTable from "../Tables/ProjectsTable";
import RFQForm from "../Forms/RFQForm";

const RFQInfoSection = ({
  primary,
  lightBg,
  imgStart,
  lightTopLine,
  lightTextDesc,
  buttonLabel,
  description,
  headline,
  lightText,
  topLine,
  img,
  alt,
  start,
}) => {
  return (
    <>
      <InfoSec
        lightBg={lightBg}
        style={{ marginBottom: "0", paddingBottom: "0" }}
      >
        <ProjectHeading
          style={{
            // align: "center",
            marginLeft: "0",
            fontSize: "30px",
            marginBottom: "0",
            textAlign: "center",
          }}
        >
          Create a Project RFQ
        </ProjectHeading>
        <Para>
          Create a Project Request for Quote(RFQ) for users to submit their
          quotes. Relevant users will be notified of your request.
        </Para>
      </InfoSec>
      <InfoSec lightBg={lightBg} style={{ marginTop: "0", paddingTop: "40px" }}>
        <RFQForm />
      </InfoSec>
    </>
  );
};

export default RFQInfoSection;
