import styled from "styled-components";

export const SearchBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
`;

export const SearchInputBox = styled.div`
  width: 500px;
  display: flex;
  height: 60px;
  background-color: white;
  border-radius: 30px;
  padding: 10px;
  padding-left: 20px;

  @media screen and(max-width: 960px) {
    width: 250px;
  }
`;

export const SearchInput = styled.input`
  flex: 1;
  height: 40px;
  border: none;
  outline: none;
  font-size: 18px;
  padding-right: 10px;
`;

export const SearchIconBtnWrap = styled.div`
  background: #44c0c4;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  padding: 10px;

  &:hover {
    background: #fff;
    color: #44c0c4;
    outline: 1px solid #44c0c4;
    }
  }
`;

export const SearchIconBtn = styled.div`
  font-size: 18px;
  color: #fff;
  cursor: pointer;

  &:hover {
    color: #44c0c4;
  }
`;
