import React from "react";
import {
  InfoSec,
  InfoRow,
  InfoColumn,
  TextWrapper,
  TopLine,
  Heading,
  Subtitle,
  ImgWrapper,
  Img,
  ProjectHeading,
} from "./InfoSectionElements";

import { Container } from "../../globalStyles";
import Dropdown from "../Dropdown/Dropdown.js";
import projectsProp from "../Dropdown/Data/projectsProp.json";
import ProjectsTable from "../Tables/ProjectsTable";

const ProjectInfoSection = ({
  primary,
  lightBg,
  imgStart,
  lightTopLine,
  lightTextDesc,
  buttonLabel,
  description,
  headline,
  lightText,
  topLine,
  img,
  alt,
  start,
}) => {
  return (
    <>
      <InfoSec
        lightBg={lightBg}
        style={{ marginBottom: "0", paddingBottom: "0" }}
      >
        <ProjectHeading
          style={{
            align: "center",
            marginLeft: "40%",
            fontSize: "30px",
          }}
        >
          {headline}
        </ProjectHeading>

        {/* <Container style={{ width: "1000px" }}>
          <flexContainer style={{ display: "flex" }}>
            <Dropdown
              projectsProp={projectsProp}
              style={{ marginLeft: "0", marginRight: "0", padding: "0" }}
            />
          </flexContainer>
        </Container> */}
      </InfoSec>
      <InfoSec lightBg={lightBg}>
        <ProjectsTable style={{ maxWidth: "80%" }} />
      </InfoSec>
    </>
  );
};

export default ProjectInfoSection;
