import React from "react";
import ColumnSection from "../../components/InfoSection/ExploreInfo/ColumnSection";
import InformationSection from "../../components/InfoSection/ExploreInfo/InformationSection";
import VideoSection from "../../components/InfoSection/ExploreInfo/VideoSection";
import PageInfoSection from "../../components/InfoSection/PageInfoSection";
import { columnObjOne, exploreObj, homeObjOne } from "./Data";

const Explore = () => {
  return (
    <>
      <PageInfoSection {...homeObjOne} />
      <InformationSection {...exploreObj} />
      <VideoSection />
      <ColumnSection {...columnObjOne} />
    </>
  );
};

export default Explore;
