import React, { useState, useEffect } from "react";
import { FaBars, FaTimes } from "react-icons/fa";
import { IconContext } from "react-icons/lib";
import { isAuthenticated } from "../../core";
import GlobalStyle, { Button } from "../../globalStyles";
import { animateScroll as scroll } from "react-scroll";
import userIcon from "../../Images/userIconPlaceholder.png";
import { Img } from "../InfoSection/InfoSectionElements";
import LoginModal from "../Modals/LoginModal";
import Dropdown from "./Dropdown";
import {
  Nav,
  NavbarContainer,
  NavLogo,
  LogoImg,
  MobileIcon,
  NavMenu,
  NavItem,
  NavLinks,
  NavLinksLog,
  NavItemBtn,
  NavBtnLink,
  DropdownB,
  Dropbtn,
  DropdownContent,
  HoverBtn,
  Da,
  lia
} from "./NavbarElements";
import {Select} from "../../components/Forms/RFQFormElements";

const Navbar = () => {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const [dropdown, setDropdown] = useState(false);

  const onMouseEnter = () => {
    if (window.innerWidth < 960) {
      setDropdown(false);
    } else {
      setDropdown(true);
    }
  };

  const onMouseLeave = () => {
    if (window.innerWidth < 960) {
      setDropdown(false);
    } else {
      setDropdown(false);
    }
  };

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  {
  }

  const [authenticated, setAuthenticated] = useState(null);
  const [user, setUser] = useState(null);

  useEffect(async () => {
    const result = await isAuthenticated();
    setAuthenticated(result);
  }, []);

  function login() {}
  function logout() {}

  // useEffect(() => {
  //   if (authenticated) {
  //     auth.getUser().then(setUser);
  //   } else {
  //     setUser(null);
  //   }
  // }, [authenticated]);
  const [scrollNav, setScrollNav] = useState(false);

  const changeNav = () => {
    if (window.scrollY >= 80) {
      setScrollNav(true);
    } else {
      setScrollNav(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", changeNav);
  }, []);

  const toggleHome = () => {
    scroll.scrollToTop();
  };
  const toggleExplore = () => {
    scroll.scrollToTop();
  };
  const toggleProjects = () => {
    scroll.scrollToTop();
  };

  window.addEventListener("resize", showButton);

  const [showModal, setShowModal] = useState(false);
  const openModal = () => {
    setShowModal((prev) => !prev);
  };

  return (
    <>
      <IconContext.Provider value={{ color: "#fff" }}>
        <Nav>
          <NavbarContainer>
            <NavLogo to="/" onClick={toggleHome}>
              {" "}
              {/*onClick={closeMobileMenu}*/}
              <LogoImg />
            </NavLogo>
            <MobileIcon onClick={handleClick}>
              {click ? <FaTimes /> : <FaBars />}
            </MobileIcon>
            <NavMenu onClick={handleClick} click={click}>
              <NavItem>
                <NavLinks
                  to="/RFQ"
                  onClick={toggleExplore}
                  activeStyle={{ color: "#44c0c4" }}
                >
                  RFQ
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinks
                  to="/projects"
                  onClick={toggleProjects}
                  activeStyle={{ color: "#44c0c4" }}
                >
                  Search
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinksLog onClick={openModal} to="">
                  Login
                </NavLinksLog>
                <LoginModal showModal={showModal} setShowModal={setShowModal} />
                <GlobalStyle />
              </NavItem>

              {/* WILL WORK WHEN DATABASE IS ADDED */}

              {/* <NavItemBtn>
                {isAuthenticated !== null && (
                  <button
                    onClick={() =>
                      isAuthenticated ? auth.logout() : auth.login()
                    }
                    className="App-link"
                  >
                    Log {authenticated ? "out" : "in"}
                  </button>
                )}
              </NavItemBtn> */}
            </NavMenu>
            <NavMenu style={{ "margin-right": "-24px;" }}>
              <NavItemBtn
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
              >
                {button ? (
                  authenticated ? (
                    <NavBtnLink to="">
                      <Button
                        fontBig
                        primary
                        onClick={login}
                        style={{
                          borderRadius: "30px",
                          backgroundColor: "#fff"
                        }}
                      >
                        <FaBars style={{ color: "#101522" }} />
                        <Img
                          src={userIcon}
                          alt=""
                          style={{
                            borderRadius: "50%",
                            marginLeft: "15px",
                            paddingRight: "0",
                            border: "0",
                            width: "28px",
                            height: "28px",
                          }}
                        />
                        <HoverBtn> 
                         {dropdown   && <Dropdown />}    
                         </HoverBtn>                    
                      </Button>
                     
                    </NavBtnLink>
                  ) : (
                    <NavBtnLink to="/contact">
                      <Button fontBig primary onClick={logout}>
                        logout
                      </Button>
                    </NavBtnLink>
                  )
                ) : (
                  ""
                )}
              </NavItemBtn>

              {/* <NavItemBtn>
             
                  <Select style={{
                    color: "#9eacb7",
                    borderRadius: "45%",
                    fontWeight: "100",
                    fontSize: "16px",
                    backgroundColor: "#fff",
                    border: "0px",
                    outline: "0px",
                    width: "200px",
                    height: "50px",
                  }}>
                    <option style={{
                      color: "#9eacb7",
                      width: "500px",
                      backgroundColor: "#fff",
                      padding: "5px",
                    }}>Word</option>  
                    <option style={{
                      color: "#9eacb7",
                      width: "500px",
                      backgroundColor: "#fff",
                      padding: "5px",
                    }}>worddd</option>  
                    <option style={{
                      color: "#9eacb7",
                      width: "500px",
                      backgroundColor: "#fff",
                      padding: "5px",
                    }}>worddd</option>
                  </Select>                

              </NavItemBtn> */}

            </NavMenu>
          </NavbarContainer>
        </Nav>
      </IconContext.Provider>
    </>
  );
};

export default Navbar;
