import React from "react";
import { InfoSec, ProjectHeading } from "./InfoSectionElements";
import { BackPageButton } from "../Modals/ModalElements";
import { Link } from "react-router-dom";
import RFQItemForm from "../Forms/RFQItemForm";

const RFQSpecInfoSection = ({ lightBg }) => {
  return (
    <>
      <InfoSec
        lightBg={lightBg}
        style={{ marginBottom: "0", paddingBottom: "0", paddingTop: "0" }}
      >
        <Link to="/RFQ">
          <BackPageButton style={{ marginLeft: "30%", marginBottom: "50px" }} />{" "}
        </Link>

        <ProjectHeading
          style={{
            // align: "center",
            marginLeft: "0",
            fontSize: "30px",
            marginBottom: "0",
            textAlign: "center",
          }}
        >
          Item RFQ Specifications
        </ProjectHeading>
        <p
          style={{
            marginLeft: "30%",
            width: "40%",
            textAlign: "center",
            color: "#9eacb7",
          }}
        >
          Enter the specificaions and optional media of your Request to assist users clarify the details of your project
        </p>
      </InfoSec>
      <InfoSec lightBg={lightBg} style={{ marginTop: "0", paddingTop: "20px" }}>
        <RFQItemForm />
      </InfoSec>
    </>
  );
};

export default RFQSpecInfoSection;
