import React from 'react'
import { Select } from '../../components/Forms/RFQFormElements'
import TabMenu from '../../components/TabMenus/TabMenu'
import { Clearfix, FloatContainer, LeftFloat, PageBg,RightFloat,TextContainer} from './BidroomElements'
import {FaBars, FaMapMarkerAlt, FaTh} from "react-icons/fa";
import Interests from '../../components/InfoSection/Interests/Interests';
import ProjAdded from '../../components/InfoSection/Interests/ProjAdded';

const Bidroom = ({
    darkBg
}) => {
    
    return (
        <PageBg darkBg={darkBg}>
            <TextContainer>
                     <p style={{color: "grey",fontSize: "16px", marginBottom: "10px"}}>
                         Your Bid Activity
                     </p>
                    <h1 style={{color:"#fff"}}>Bid Room</h1>
                    
                    
                      <Clearfix>
	<FloatContainer>
<LeftFloat>
    <p style={{color: "grey",fontSize: "14px",marginTop: "30px",marginBottom: "15px"}}>
                      Displaying  of  Results
                      </p>
                      </LeftFloat>

<RightFloat style={{float:"right"}}>
    <Select style={{marginLeft:"0",marginRight:"0",width: "300px"}}>
        <option>Sort By: Value</option>
        <option>Sort By: Amount</option>
        <option>Sort By: Location</option>
    </Select>
	<button style={{
        marginLeft: "20px",
       backgroundColor: "#25323d",
       border: "none",
       color: "white",
       width: "50px",
       height: "50px",
       padding: "15px 15px",
       textAlign: "center",
       borderRadius: "5px",
       textDecoration: "none",
       display: "inline-block",
       fontSize: "18px",
       margin: "4px 2px",
       cursor: "pointer" 
    }}><FaTh /></button>
    <button style={{
        marginLeft: "8px",
       backgroundColor: "#25323d",
       border: "none",
       color: "white",
       width: "50px",
       height: "50px",
       padding: "15px 15px",
       textAlign: "center",
       borderRadius: "5px",
       textDecoration: "none",
       display: "inline-block",
       fontSize: "18px",
       margin: "4px 2px",
       cursor: "pointer" 
    }}><FaBars /></button>
    <button style={{
        marginLeft: "10px",
       backgroundColor: "#25323d",
       border: "none",
       color: "white",
       width: "50px",
       height: "50px",
       padding: "15px 15px",
       textAlign: "center",
       borderRadius: "5px",
       textDecoration: "none",
       display: "inline-block",
       fontSize: "18px",
       margin: "4px 2px",
       cursor: "pointer" 
    }}><FaMapMarkerAlt /></button>
</RightFloat>
    </FloatContainer>
</Clearfix>


            </TextContainer>
                      
            <TabMenu />
            <ProjAdded />


        
            
        </PageBg>
    )
}

export default Bidroom;
