import React from "react";
import { useState, useEffect } from "react";
import { animateScroll as scroll } from "react-scroll";
import { FaFacebook, FaInstagram, FaLinkedin, FaTwitter } from "react-icons/fa";
import {
  FooterContainer,
  FooterWrap,
  FooterLinksContainer,
  FooterLinksWrapper,
  FooterLinkItems,
  FooterLinkTitle,
  FooterLink,
  SocialMedia,
  SocialMediaWrap,
  SocialLogo,
  WebsiteRights,
  SocialIcons,
  SocialIconLink,
} from "./FooterElements";

import { LogoImg } from "../Navbar/NavbarElements";

const Footer = () => {
  const [scrollNav, setScrollNav] = useState(false);

  const changeNav = () => {
    if (window.scrollY >= 80) {
      setScrollNav(true);
    } else {
      setScrollNav(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", changeNav);
  }, []);

  const toggleHome = () => {
    scroll.scrollToTop();
  };

  return (
    <FooterContainer>
      <FooterWrap>
        <FooterLinksContainer>
          <FooterLinksWrapper>
            <FooterLinkItems>
              <FooterLinkTitle>About</FooterLinkTitle>
              <FooterLink to="/Explore" onClick={toggleHome}>
                About Us
              </FooterLink>
              <FooterLink to="/signin">How Afriserv Works</FooterLink>
              <FooterLink to="/signin">FAQ</FooterLink>
              <FooterLink to="/signin">Privacy Policy</FooterLink>
              <FooterLink to="/signin">Terms of Service</FooterLink>
            </FooterLinkItems>
            <FooterLinkItems>
              <FooterLinkTitle>Find</FooterLinkTitle>
              <FooterLink to="/signin">Projects</FooterLink>
              <FooterLink to="/signin">Contractors</FooterLink>
              <FooterLink to="/signin">Suppliers</FooterLink>
            </FooterLinkItems>
          </FooterLinksWrapper>
          <FooterLinksWrapper>
            <FooterLinkItems>
              <FooterLinkTitle>Support</FooterLinkTitle>
              <FooterLink to="/signin">Covid 19</FooterLink>
              <FooterLink to="/signin">FAQ</FooterLink>
              <FooterLink to="/signin">Contact</FooterLink>
              <FooterLink to="/signin">Privacy Policy</FooterLink>
              <FooterLink to="/signin">Legal</FooterLink>
            </FooterLinkItems>
            <FooterLinkItems>
              <FooterLinkTitle>Support</FooterLinkTitle>
              <FooterLink to="/signin">Covid 19</FooterLink>
              <FooterLink to="/signin">FAQ</FooterLink>
              <FooterLink to="/signin">Contact</FooterLink>
              <FooterLink to="/signin">Privacy Policy</FooterLink>
              <FooterLink to="/signin">Legal</FooterLink>
            </FooterLinkItems>
          </FooterLinksWrapper>
        </FooterLinksContainer>

        <hr
          style={{
            width: "100%",
            backgroundColor: "#101522",
          }}
        />
        <SocialMedia>
          <SocialMediaWrap>
            {/* <SocialLogo to="/">
             
              <LogoImg style={{ width: "160", height: "70" }} />
            </SocialLogo> */}
            <WebsiteRights>
              {new Date().getFullYear()} Afriserv &copy; PTY LTD All rights
              reserved.
            </WebsiteRights>
            <SocialIcons>
              <SocialIconLink href="/" aria-label="Facebook" target="_blank">
                <FaFacebook />
              </SocialIconLink>
              <SocialIconLink href="/" aria-label="Instagram" target="_blank">
                <FaInstagram />
              </SocialIconLink>
              <SocialIconLink href="/" aria-label="Linkedin" target="_blank">
                <FaLinkedin />
              </SocialIconLink>
              <SocialIconLink href="/" aria-label="Twitter" target="_blank">
                <FaTwitter />
              </SocialIconLink>
            </SocialIcons>
          </SocialMediaWrap>
        </SocialMedia>
      </FooterWrap>
    </FooterContainer>
  );
};

export default Footer;
