import React, { useState, useEffect } from "react";
import { animateScroll as scroll } from "react-scroll";
import { FaBars, FaTimes } from "react-icons/fa";
import { IconContext } from "react-icons/lib";
import { isAuthenticated } from "../../core";
import GlobalStyle, { Button } from "../../globalStyles";
import FormModal from "../Modals/FormModal";
import { Link } from "react-router-dom";
import {
  Select,
  RFQDesc,
  RFQMiniStatement,
  RFQSearchbar,
  SearchInputBox,
  SearchInput,
} from "./RFQFormElements";
import { FaSearch } from "react-icons/fa";

const RFQForm = () => {
  const toggleTop = () => {
    scroll.scrollToTop();
  };
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener("resize", showButton);

  const [showModal, setShowModal] = useState(false);
  const openModal = () => {
    setShowModal((prev) => !prev);
  };

  return (
    <>
      <Select>
        <option>Your Project Role</option>
        <option>Supplier</option>
        <option>Contractor</option>
      </Select>
      <RFQDesc>
        What type of businesses will your business be trading as
      </RFQDesc>

      <Select>
        <option>Information Exchange</option>
        <option>Supplier</option>
        <option>Contractor</option>
      </Select>
      <RFQDesc>Select how you would like your quotes to be submitted</RFQDesc>

      <Select>
        <option>Looking For..</option>
        <option>Supplier</option>
        <option>Contractor</option>
      </Select>
      <RFQDesc>Select who this quote is aimed at</RFQDesc>

      <Select>
        <option>Type of Bid</option>
        <option>Supplier</option>
        <option>Contractor</option>
      </Select>
      <RFQDesc>
        Private Bids allow for selected profile submissions only, Public Bids
        allow for any Afriserv member submissions.
      </RFQDesc>

      <Link
        onClick={toggleTop}
        style={{ textDecoration: "none", color: "#fff" }}
        to="/RFQCreate"
      >
        <Button
          style={{
            marginBottom: "20px",
            marginLeft: "30%",
            width: "40%",
            height: "50px",
          }}
        >
          Create a New Project
        </Button>
      </Link>
      <RFQMiniStatement>Or</RFQMiniStatement>

      <RFQSearchbar>
        <SearchInputBox>
          <FaSearch
            aria-hidden="true"
            style={{
              color: "#44c0c4",
              fontSize: "20px",
              marginTop: "14px",
              marginLeft: "10%",
              marginRight: "2%",
            }}
          />
          <SearchInput
            onClick={openModal}
            to=""
            type="text"
            name=" "
            placeholder="Add to an existing Project"
          />
        </SearchInputBox>
        <FormModal showModal={showModal} setShowModal={setShowModal} />
        <GlobalStyle />
      </RFQSearchbar>
    </>
  );
};

export default RFQForm;
