import React from "react";
import {
  InfoContainer,
  InfoWrapper,
  InfoRow,
  Column1,
  Column2,
  TextWrapper,
  TopLine,
  Heading,
  Subtitle,
  BtnWrap,
  ImgWrap,
  Img,
} from "./InfoElements";

const ColumnSection = ({
  lightBg,
  id,
  imgStart,
  topLine,
  lightText,
  headline,
  headline2,
  darkText,
  description,
  description2,
  buttonLabel,
  img,
  alt,
  primary,
  dark,
  dark2,
}) => {
  return (
    <>
      <InfoContainer
        lightBg={lightBg}
        id={id}
        style={{ marginBottom: "200px" }}
      >
        <InfoWrapper>
          <InfoRow imgStart={imgStart}>
            <Column1>
              <Img src={img} alt={alt} />
              <TextWrapper>
                <TopLine>{topLine}</TopLine>
                <Heading lightText={lightText}>{headline}</Heading>
                <Subtitle darkText={darkText}>{description}</Subtitle>
              </TextWrapper>
            </Column1>
            <Column2>
              <ImgWrap>
                <Img src={img} alt={alt} />
                <TextWrapper>
                  <TopLine>{topLine}</TopLine>
                  <Heading lightText={lightText}>{headline2}</Heading>
                  <Subtitle darkText={darkText}>{description2}</Subtitle>
                </TextWrapper>
              </ImgWrap>
            </Column2>
          </InfoRow>
        </InfoWrapper>
      </InfoContainer>
    </>
  );
};

export default ColumnSection;
