import styled from "styled-components";

export const Select = styled.select`
  color: #9eacb7;
  margin-left: 30%;
  margin-right: 30%;
  font-weight: 100;
  font-size: 16px;
  background-color: #25323d;
  border: 0px;
  outline: 0px;
  width: 40%;
  height: 50px;
  border-radius: 5px;
  padding-left: 10px;

  @media screen and (max-width: 960px) {
    margin-left: 10%;
    margin-right: 10%;
    width: 75%;

  }

  &:hover {
  }

  option {
    color: #fff; // #363942;
    width: 100%;
    background-color: #101522;
    padding: 5px;
  }
`;

export const RFQDesc = styled.p`
  font-size: 14px;
  color: #9eacb7;
  margin-bottom: 40px;
  width: 40%;
  margin-left: 30%;

  
  @media screen and (max-width: 960px) {
    margin-left: 10%;
    margin-right: 10%;
    width: 75%;
  }
`;

export const RFQMiniStatement = styled.div`
  margin-left: 30%;
  text-align: center;
  width: 40%;
  margin-bottom: 20px;
`;

export const RFQSearchbar = styled.div`
  margin-left: 30%;
  text-align: center;
  width: 40%;
  margin-bottom: 40px;
  border-radius: 5px;
`;

export const SearchInputBox = styled.div`
  width: 100%;
  display: flex;
  height: 100%;
  background-color: #25323d;
  border-radius: 5px;
`;

export const SearchInput = styled.input`
  flex: 1;
  height: 50px;
  border: none;
  outline: none;
  font-size: 18px;
  padding-right: 10px;
  background-color: #25323d;
  text-align: 50%;
  border-radius: 5px;
`;

export const RFQInput = styled.input`
  flex: 1;
  height: 50px;
  outline: none;
  color: #fff;
  font-size: 14px;
  padding-right: 10px;
  background-color: #101522;
  border: 1px solid #25323d;
  //   text-align: left;
  width: 40%;
  margin-left: 30%;
  margin-right: 30%;
  border-radius: 5px;
  margin-bottom: 20px;
  padding-left: 10px;
`;

export const RFQTextArea = styled.textarea`
  flex: 1;
  color: #fff;
  height: 200px;
  outline: none;
  font-size: 14px;
  padding-right: 10px;
  background-color: #101522;
  border: 1px solid #25323d;
  //   text-align: left;
  width: 40%;
  margin-left: 30%;
  margin-right: 30%;
  border-radius: 5px;
  margin-bottom: 20px;
  padding-left: 10px;
  padding-top: 5px;
`;
